import React, { Component } from "react";
import { createNumberMask } from "redux-form-input-masks";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import AccordCashMurni from "../../FICO/Element/AccordCashMurni";
import AccordTransferMurni from "../../FICO/Element/AccordTransferMurni";
import AccordGiroMurni from "../../FICO/Element/AccordGiroMurni";
import Swal from "sweetalert2";
import { getTotalBayarLM } from "../../actions/transaksuPenjualanLMAction";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
class FormModalPembayaranPembelianLM extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  setCash(data) {
    var sampai = {
      tunaiRp: data.cash,
      tunaiNetto: data.cashNetto,
      tunaiKurs: 0,
      tunaiTipe: "RUPIAH",
    };
    var hasil = [];
    hasil.push(sampai);
    localStorage.setItem("cashLM", data.cashNetto);
    localStorage.setItem("alatBayarTunaiLM", JSON.stringify(hasil));
    Swal.fire({
      position: "top-right",
      icon: "success",
      text: "Pembayaran berhasil",
      showConfirmButton: false,
      timer: 1500,
    }).then(() => this.props.dispatch(getTotalBayarLM()));
  }
  setTransfer(data) {
    var sampai = {
      transferRp: data.transfer,
      transferNetto: data.transfernetto,
      transferKurs: 0,
      transferTanggalJanji: data.tanggal,
      transferTipe: "-",
      accFrom: data.kodebank,
      accTo: data.tujuan,
    };
    var hasil = [];
    hasil.push(sampai);
    localStorage.setItem("alatBayarTrfLM", JSON.stringify(hasil));
    localStorage.setItem("transferLM", data.transfernetto);
    Swal.fire({
      position: "top-right",
      icon: "success",
      text: "Pembayaran berhasil",
      showConfirmButton: false,
      timer: 1500,
    }).then(() => this.props.dispatch(getTotalBayarLM()));
  }
  setGiro(data) {
    var sampai = {
      girorRp: data.giro,
      giroNetto: data.giroNetto,
      giroKurs: 0,
      giroTanggalJanji: data.tanggal,
      giroBank: data.bankGiro,
      giroTipe: "-",
    };
    var hasil = [];
    hasil.push(sampai);
    localStorage.setItem("giroLM", data.giroNetto);
    localStorage.setItem("alatBayarGiroLM", JSON.stringify(hasil));
    Swal.fire({
      position: "top-right",
      icon: "success",
      text: "Pembayaran berhasil",
      showConfirmButton: false,
      timer: 1500,
    }).then(() => this.props.dispatch(getTotalBayarLM()));
  }
  componentDidMount() {
    this.update();
  }
  update() {
    var giro = JSON.parse(localStorage.getItem("alatBayarGiroLM"));
    var cash = JSON.parse(localStorage.getItem("alatBayarTunaiLM"));
    var transfer = JSON.parse(localStorage.getItem("alatBayarTrfLM"));
    var sumGiro = giro === null ? 0 : giro[0].giroNetto;
    var sumCash = cash === null ? 0 : cash[0].tunaiNetto;
    var sumtransfer = transfer === null ? 0 : transfer[0].transferRp;
    this.props.change("totalBayar", sumGiro + sumCash + sumtransfer);
  }
  render() {
    return (
      <div>
        <div className="col-lg-12">
          <div className="row">
            <AccordCashMurni onSubmit={(data) => this.setCash(data)} />
            <AccordTransferMurni onSubmit={(data) => this.setTransfer(data)} />
            <AccordGiroMurni onSubmit={(data) => this.setGiro(data)} />
            <div className="col-lg-6"></div>
              <div className="col-lg-12">
              <form onSubmit={this.props.handleSubmit}>
                <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Total Bon (Rp)</label>
              <Field
                name="totalBon"
                label="Total Bon (Rp)"
                component="input"
                type="telp"
                className="form-control"
                {...currencyMask}
                onBlur={this.props.change("totalBon", this.props.totalBon)}
              />
            </div>
            <div className="col-lg-4 d-none">
              <label htmlFor="">Total Bon (Rp)</label>
              <Field
                name="noBon"
                label="Total Bon (Rp)"
                component="input"
                type="telp"
                className="form-control"
                {...currencyMask}
                onBlur={this.props.change("noBon", this.props.noBon)}
              />
            </div>
            <div className="col-lg-4">
              <label htmlFor="">Total Bayar (Rp)</label>
              <Field
                name="totalBayar"
                label="Total Bayar (Rp)"
                component="input"
                type="telp"
                className="form-control"
                {...currencyMask}
                onBlur={this.update()}
              />
            </div>
            <div className="col-lg-4">
              <label htmlFor="">Total Bon (Rp)</label>
              <Field
                name="sisaBon"
                label="Sisa Bon (Rp)"
                component="input"
                type="telp"
                className="form-control"
                {...currencyMask}
                onBlur={this.props.change("sisaBon", this.props.sisaBon)}
              />
            </div>
            <div className="col-lg-12 mt-4">
              <div className="text-right">
                <button className="btn btn-primary">Simpan</button>
              </div>
            </div>
            </div>
            </form>
              </div>
          </div>
        </div>
      </div>
    );
  }
}

FormModalPembayaranPembelianLM = reduxForm({
  form: "FormModalPembayaranPembelianLM",
  enableReinitialize: true,
})(FormModalPembayaranPembelianLM);
const selector = formValueSelector("FormModalPembayaranPembelianLM"); // <-- same as form name
export default connect((state) => {
  const { totalBon, totalBayar } = selector(state, "totalBon", "totalBayar");
  return {
    sisaBon: parseFloat(totalBon) - parseFloat(totalBayar),
  };
})(FormModalPembayaranPembelianLM);
